.Score-controls {
  margin: 0 auto;
  width: 75%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Score-controls span {
  -ms-flex: 1 0 0%;
      flex: 1 0 0%;
  margin-right: 3px;
  padding: 10px;
  background-color: hsla(81, 28%, 90%, .6);
  border-radius: 2px;
  font-size: 1.5em;
  color: hsla(120, 2%, 20%, 1);
}

.Score-controls span:hover {
  background-color: hsla(81, 28%, 90%, .9);
  cursor: pointer;
}


